import React from "react";
import styles from "./Project.module.css";
import Slider from "react-slick";

type PropsType = {
    imgUrl: string[]
    title: string
    href: string
}

const Project: React.FC<PropsType> = (props) => {


    // Slider settings
    const settings = {
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true,
        dots: false,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 4000,
        speed: 3000
    }


    // Map slides
    const slides = props.imgUrl.map((item, index) => {
        return (
            <div className={styles.slideWrapper}>
                <img key={+new Date()}
                     className={styles.slide}
                     src={item}
                     alt={`slide ${index}`}/>
            </div>
        )
    })


    // Render
    return (
        <div className={styles.wrapper}>

            <a href={props.href}>
                <Slider {...settings}>
                    {slides}
                </Slider>
            </a>

            <div className={styles.description}>
                <h3 className={styles.title}>
                    {props.title}
                </h3>
                <p className={styles.text}>
                    {props.children}
                </p>
            </div>
        </div>
    )
}

export default Project;
